.polls li {
    background-color: var(--lignt);
    color: var(--blue);
    padding: 0.5rem;
    text-align: center;
}

.polls li:nth-child(odd) {
    background-color: #fff;
}

.polls li:hover {
    border: 2px solid var(--blue);
}

.poll-title {
    color: var(--blue);
    font-size: 2rem;
    margin-top: 1rem;
    text-align: center;
    text-transform: uppercase;
}