:root {
    --danger: #ff8a80;
    --blue: #0d47a1;
    --lignt: #bbdefb;
}

body {
    background-color: #bbdefb;
    background-color: var(--lignt);
}
* {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    list-style: none;
}
.container {
    margin: auto;
    width: 80%;
}
.error {
    background-color: var(--danger);
    text-align: center;
    color: white;
    width: 70%;
    margin: 1rem auto;
    padding: 0.5rem 0;
    border-radius: 0.5rem;
}
.navbar {
    background-color: var(--blue);
    padding: 1rem 0;
}

.navbar-brand {
    background-color: var(--lignt);
    color: var(--blue);
    text-decoration: none;
    padding: 0.5rem;
    text-transform: uppercase;
    font-weight: bold;
}

.navbar-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.navbar-item {
    color: white;
    text-decoration: none;
    cursor: pointer;
}

.navbar-user {
    margin-top: 0;
    text-align: right;
    color: var(--lignt);
}
.form {
    width: 50%;
    margin: 1rem auto;
    background-color: var(--blue);
    padding: 1rem;
    border-radius: 0.4rem;
}

.form-input {
    width: 100%;
    padding: 0.5rem 1rem;
    margin: 0.5rem 0;
    border: 1px solid var(--lignt);
    border-radius: 0.4rem;
    box-sizing: border-box;
    outline: none;
    font-size: 1rem;
}

.form-label {
    color: white;
    text-transform: uppercase;
    font-size: 0.8rem;
}
.button {
    background-color: var(--lignt);
    border: 2px solid var(--blue);
    border-radius: 0.5rem;
    color: var(--blue);
    font-size: 1rem;
    margin: 1rem;
    padding: 0.2rem 1rem;
    transition: all 0.4s ease-out;
}

.button:hover {
    background-color: white;
}

.button_center {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.polls li {
    background-color: var(--lignt);
    color: var(--blue);
    padding: 0.5rem;
    text-align: center;
}

.polls li:nth-child(odd) {
    background-color: #fff;
}

.polls li:hover {
    border: 2px solid var(--blue);
}

.poll-title {
    color: var(--blue);
    font-size: 2rem;
    margin-top: 1rem;
    text-align: center;
    text-transform: uppercase;
}


